import React, { useState, useEffect } from "react";
import UploadService from "../services/FileUploadService";
import {
    StackedCarousel,
    ResponsiveContainer
} from "react-stacked-center-carousel";
import Fab from "@material-ui/core/Fab";
import KeyboardArrowLeftIcon from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
import "./Slide.css";
import { Slide } from "./Slide";


const UploadFiles = () => {
    const [selectedFiles, setSelectedFiles] = useState(undefined);
    const [currentFile, setCurrentFile] = useState(undefined);
    const [progress, setProgress] = useState(0);
    const [messageUpload, setMessageUpload] = useState("");
    const [status, setStatus] = useState("");
    const [data, setData] = useState([]);

    useEffect(() => {
        setStatus("-");
    }, []);

    function Pagination(props) {
        const { centerSlideDataIndex, updatePosition } = props;
        console.log("ðŸš€ ~ centerSlideDataIndex--000", centerSlideDataIndex);
        return (
            <div
                style={{
                    display: "flex",
                    justifyContent: "center",
                    gap: "20px",
                    marginTop: 20,
                    marginBottom: 20
                }}
            >
                {data.map((_, index) => {
                    const isCenterSlide = props.centerSlideDataIndex === index;
                    return (
                        <div
                            onClick={() => {
                                updatePosition(index);
                            }}
                            style={{
                                height: 15,
                                width: 15,
                                borderRadius: "100%",
                                background: isCenterSlide ? "white" : "none",
                                border: "1px solid white",
                                cursor: "pointer"
                            }}
                        />
                    );
                })}
            </div>
        );
    }

    const ref = React.useRef(StackedCarousel);
    const [centerSlideDataIndex, setCenterSlideDataIndex] = React.useState(0);
    const onCenterSlideDataIndexChange = (newIndex) => {
        setCenterSlideDataIndex(newIndex);
    };

    const updatePosition = (index) => {
        console.log("ðŸš€ ~ ition ~ index", index);
        console.log("ðŸš€ ~centerSlideDataIndex--111", centerSlideDataIndex);
        console.log("ðŸš€ subtract", index - centerSlideDataIndex);
        // eslint-disable-next-line no-unused-expressions
        ref?.current?.swipeTo(index - centerSlideDataIndex);
    };


    const selectFile = (event) => {
        if (event.target.files.length === 1) {
            setSelectedFiles(event.target.files);
        } else {
            setMessageUpload("Please choose only 1 file")
        }
    };

    const upload = () => {
        let currentFile = selectedFiles[0];

        setProgress(0);
        setCurrentFile(currentFile);
        setStatus("Predicting...")
        setData([])

        UploadService.upload(currentFile, (event) => {
            setProgress(Math.round((50 * event.loaded) / event.total));
        }, (event) => {
            setProgress(Math.round((100 * event.loaded) / event.total));
        })
            .then((response) => {
                let result = [...response.data]
                while (result.length < 3) {
                    result.push({
                        image_id: "blank.jpg",
                        document_type: "This file only have",
                        confidence_level: response.data.length + " page"
                    })
                }
                setData(result)
                setStatus("Success!");
            })
            .catch(() => {
                setProgress(0);
                setStatus("Could not upload the file!");
                setCurrentFile(undefined);
            });

        setSelectedFiles(undefined);
    };
    useEffect(() => {
        setMessageUpload("No file selected.")
    }, []);

    return (
        <div>
            {
                data.length !== 0 &&
                <div>
                    <div style={{ width: "100%", position: "relative" }}>
                        <ResponsiveContainer
                            carouselRef={ref}
                            render={(width, carouselRef) => {
                                return (
                                    <StackedCarousel
                                        ref={carouselRef}
                                        slideComponent={Slide}
                                        slideWidth={300}
                                        carouselWidth={width}
                                        height={500}
                                        data={data}
                                        maxVisibleSlide={5}
                                        disableSwipe
                                        customScales={[1, 0.85, 0.7, 0.55]}
                                        transitionTime={450}
                                        onActiveSlideChange={onCenterSlideDataIndexChange}
                                    />
                                );
                            }}
                        />
                    </div>
                    <div style={{ display: "inline-flex" }}>
                        <Fab
                            className="card-button left"
                            size="small"
                            onClick={() => ref.current?.goBack()}
                            style={{ margin: "10px" }}
                        >
                            <KeyboardArrowLeftIcon style={{ fontSize: 30 }} />
                        </Fab>
                        <Pagination
                            updatePosition={updatePosition}
                            centerSlideDataIndex={centerSlideDataIndex}
                        />
                        <Fab
                            className="card-button right"
                            size="small"
                            onClick={() => ref.current?.goNext()}
                            style={{ margin: "10px" }}
                        >
                            <KeyboardArrowRightIcon style={{ fontSize: 30 }} />
                        </Fab>
                    </div>
                </div>
            }

            {currentFile && (
                <div className="progress">
                    <div
                        className="progress-bar progress-bar-info progress-bar-striped"
                        role="progressbar"
                        aria-valuenow={progress}
                        aria-valuemin="0"
                        aria-valuemax="100"
                        style={{ width: progress + "%" }}
                    >
                        {progress}%
                    </div>
                </div>
            )}

            <label className="btn btn-default">
                {/* <input type="file" onChange={selectFile} /> */}
                <label class="custom-file-upload">
                    <input type="file" onChange={selectFile} />
                    Browse
                </label>
            </label>

            <label style={{
                fontSize: '20px',
                marginRight: '10px'
            }}>
                {/* {selectedFiles.get(0).name} */}
                {
                    selectedFiles ?
                        selectedFiles[0].name :
                        messageUpload
                }
            </label>

            <button
                className="btn btn-success"
                disabled={!selectedFiles}
                onClick={upload}
            >
                Upload
            </button>
            <div>
                <div>Status: {status}</div>
            </div>
        </div>
    );
};

export default UploadFiles;